import styled from "styled-components"
import React from "react"
import {useTags} from 'hooks/useTags'

const Wrapper = styled.section`
  background:#FFF;
  padding: 12px 16px;
  flex-grow:1;
  display:flex;
  flex-direction:column;
  justify-content:end;
  align-items:flex-start;
  >ol{
    margin: 0 -12px;
    >li{
      display:inline-block;
      background:#D9D9D9;
      padding: 3px 18px;
      border-radius:18px;
      margin: 8px 12px;
      font-size:14px;
      &.selected{
        background:pink;
      }
    }
  }
  >button{
    background:none;
    border:none;
    border-bottom:1px solid #333;
    padding: 2px 4px;
    margin-top:8px;
    color:#666;
  }
`
type Props={
  value:number[],
  onChange:(selected:number[])=>void
} 

const TagsSection: React.FC<Props> = (props)=>{
  const {tags, addTag} = useTags()
  const selectTagIds = props.value
  
  

  const onToggleTag = (tagId:number)=>{
    const index = selectTagIds.indexOf(tagId)
    if(index>=0){
      props.onChange(selectTagIds.filter(t=>t!==tagId))
    }else{
      props.onChange([...selectTagIds, tagId])
    }
  }

  const getClass = (tagId:number)=> selectTagIds.indexOf(tagId)>=0 ? 'selected' : ''
  
  return(
    <Wrapper>
      <ol>
        {
          tags.map(tag=>
            <li key={tag.id} onClick={()=>{onToggleTag(tag.id)}} className={getClass(tag.id)}>
              {tag.name}
            </li>
          )
        }
      </ol>
      <button onClick={addTag}>新增标签</button>
    </Wrapper>
  )
    
}
export {TagsSection}